import Tabbar from './components/tabbar.vue'
export default {
	name: 'App',
	components: {
		Tabbar
	},
	computed: {
		tabbarShow() {
			console.log(this.$store.getters.getTabbarShow)
			return this.$store.getters.getTabbarShow
		}
	},
	watch: {
		$route(to, from) {
			if(to.path == '/' || to.path == '/service' || to.path == '/info' || to.path == '/mine') {
				this.$store.commit('updateTabbarShow', true)
			}else {
				this.$store.commit('updateTabbarShow', false)
			}
		}
	}
}



